body {
  font-family: 'Solway', serif;
  background: rgb(34, 34, 34);
}

html,
body {
  padding: 0px;
  margin: 0px;
}

.page-wrapper {
  padding: 0px 20px;
  background-image: url('../images/Speakers_on_shelf.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-y: -200px;
  overflow: scroll;
}

#landing-headline {
  padding: 10px 10px;
  width: 50%;
  font-size: 50px;
  color: black;
  text-shadow: 3px 3px rgb(179, 106, 106);
  text-align: center;
  border-radius: 7px;
  margin: 200px auto 190px auto;
  background: rgba(255, 255, 255, 0.8);
}

#nav-wrapper {
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  transition: 0.5s;
}

#brand-header {
  font-family: 'Solway', serif;
  padding: 10px;
  font-weight: bold;
  text-align: center;
  color: white;
  font-size: 35px;
}

.top-section-header {
  background: rgba(15, 15, 15, 0.9);
  padding: 20px 0px 20px 20px;
  color: white;
  font-weight: bold;
  font-size: 30px;
  line-height: 1;
  border-radius: 7px 7px 0px 0px;
}

#tl-section-wrapper {
  border-radius: 7px;
  padding-bottom: 200px;
  flex: 3;
  height: 100px;
}

#tr-section-wrapper {
  flex: 1;
  border-radius: 7px;
  padding-bottom: 200px;
  flex: 1;
  margin-left: 20px;
  height: 500px;
}

#tl-section-body {
  background: rgba(77, 17, 8, 0.9);
  border-radius: 0px 0px 7px 7px;
  padding: 25px;
  color: white;
  font-size: 30px;
  line-height: 1.4;
}

#tr-section-body {
  background: rgba(248, 245, 202, 0.9);
  border-radius: 0px 0px 7px 7px;
  padding: 25px 10px;
  color: black;
  font-size: 20px;
}

#row-1 {
  display: flex;
  flex-direction: row;
  flex: 1;
  background: transparent;
}
